$background-color: #E9ECEF;
$background-hover: #d6e2e0;

$btn-color:  #54c4aa;
$btn-hover: #3bb095;

.header__nav {
    margin-left: 30px;
    &-item {
        color: white;
        transition: 0.2s; }
    &-item:hover {
        color: $btn-color; } }

.wrapper {
    background:  $background-color;
    min-height: 345px;
    transition: 0.7s;
    &__container {
        max-width: 1200px;
        margin: 0 auto;
        margin-left: 4%;
        &-boxes {
            margin-bottom: 32px; }
        &-table {
            margin: 0 0 75px 12px; } } }

.wrapper:hover {
    background: $background-hover; }

.btn-primary {
    background: $btn-color;
    border: $btn-color;
    transition: 0.7s; }

.btn-primary:hover {
    background: $btn-hover;
    box-shadow:  1px 1px 5px rgba(0, 0, 0, 0.25); }

.card-header {
    transition: 0.7s;
    background: $background-color; }

.card-header:hover {
    background: $background-hover; }

.table-header {
    transition: 0.7s;
    background: $background-color;
    h5 {
        margin: 0; } }

.table-header:hover {
    background: $background-hover; }

.footer {
    width: 100%;
    min-height: 60px;
    background: $background-color;
    transition: 0.7s;
    box-sizing: border-box;
    p {
        margin: 0;
        padding: 0;
        width: 100%;
        box-sizing: border-box; } }

.footer:hover {
    background: $background-hover; }

.table-link {
    text-decoration: none;
    color: black; }

.table-link:hover {
    color: $btn-hover; }

@media (max-width: 575px) {
    body {
        min-width: 320px; }
    .wrapper__container {
        margin-left: 5%; }
    .wrapper__info {
        width: 90%; } }


